.space-block {
    margin: 8px 4px 0;
    padding: 4px 4px 0;
}
.svg {
    position: fixed;
    right: 0;
    top: 80px;
    width: 390px;
}

@media (min-width: 992px) {
    .svg {
        right: 0;
        top: 100px;
        width: 600px;
    }
}

@media (min-width: 1392px) {
    .svg {
        right: 0;
        top: 191px;
        width: 807px;
    }
}

